
import { defineComponent } from 'vue'
import Tree from "@/shared/components/tree/tree/tree.vue";
import TreeDataService from '@/shared/services/TreeDataService';
import { NodeData } from '@/shared/components/tree/interface';
import { COMMON_KEY_VALUE } from '@/shared/constants/common.const';
import { COM20500111_REQ_BODY, COM20500111_RES, MenuInfo } from '@/shared/type/COM20500111.type';
import { COM20500112_REQ_BODY, COM20500112_RES } from "@/shared/type/COM20500112.type";
import { COM20500121_REQ_BODY, COM20500121_RES } from '@/shared/type/COM20500121.type';
import { COM20500131_REQ_BODY, COM20500131_RES } from '@/shared/type/COM20500131.type';
import { COM20500141_REQ_BODY, COM20500141_RES } from '@/shared/type/COM20500141.type';
import { modalController } from '@ionic/vue';

const treeService = new TreeDataService();

export default defineComponent({
    name: "com205001sc01Vue",
    components: {
        Tree
    },
    data() {
        return {
            COMMON_KEY_VALUE,
            menuTree: [] as NodeData[],
            // set default root
            menuList: [
            ] as MenuInfo[],
            // set default selected at root
            selectedMenu: {
                menuID: "0",
                menuName: "Root",
                menuLevel: 0,
                menuDisplayOrder: 0,
                menuStatusCode: "01",
                parentMenuID: "",
                state: { indeterminate: false, checked: false, selected: true, expanded: false }
            } as any,
            isAddNewMenu: false,
            isEnableEdit: false,
            isInvalidInput: false,
            menuLevelData: null as any,
            totalMenuList: 0 as number,
            menuSize: 500 as number,
        }
    },
    mounted() {
        this.onGetMenuInfoList()
    },
    computed: {
        creationValue(): string {
            const { firstRegisterTellerName, firstRegisterTellerID } = this.selectedMenu
            return `${firstRegisterTellerName} (${firstRegisterTellerID})`
        },
        lastEditValue(): string {
            const { lastChangeTellerName, lastChangeTellerID } = this.selectedMenu
            return `${lastChangeTellerName} (${lastChangeTellerID})`
        },
    },
    methods: {

        async onGetMenuInfoList() {
            const reqBody: COM20500111_REQ_BODY = {
                pageNumber: 1,
                pageSize: this.menuSize
            }
            const responseData = await this.$request.getRequest('COM20500111', reqBody, false) as COM20500111_RES
            if (responseData.header.result) {
                const lists = responseData.body.list;
                // get default main Root
                let rootData = lists.filter((items: any) => items.parentMenuID == '')
                // get menu list data that status code not equal to 09 and parent menu ID is not Empty
                let menuData = lists.filter((items: any) => items.menuStatusCode !== '09' && items.parentMenuID != '')
                this.menuList = [...rootData, ...menuData]
                this.transformAndExpandTreeData(this.menuList);
                // Check Menu data when maximun more than default menusize 
                if (this.totalMenuList > this.menuSize) {
                    this.menuSize = this.totalMenuList
                    this.callBackMenuList()
                }
            }
            this.onHighLightParentNode();
        },

        transformAndExpandTreeData(dataList) {
            const transformData = treeService.transformIntoTreeData(dataList, "menuID", "parentMenuID", "menuName")
            this.menuTree = transformData
            treeService.expandAllNodes(this.menuTree)
        },

        nodeSelectAtRoot() {
            return {
                menuID: "0",
                menuName: "Root",
                menuLevel: 0,
                menuDisplayOrder: 0,
                menuStatusCode: "",
                parentMenuID: "",
                state: { indeterminate: false, checked: false, selected: true, expanded: false }
            }
        },

        async onGetMenuInfoDetail(menuItem) {
            if (this.isAddNewMenu) {
                this.diabledSelectNewMenu();
                this.onHighLightParentNode();
                return
            }
            if (this.selectedMenu) {
                this.isEnableEdit = false
                const reqBody: COM20500112_REQ_BODY = {
                    menuID: menuItem.menuID
                }
                const responseData = await this.$request.getRequest('COM20500112', reqBody, false) as COM20500112_RES
                if (responseData.header.result) {
                    // get Menu level Base On parent node for assign to child when add new menu
                    let tmpMenuLvel = Number(responseData.body.menuLevel);
                    this.menuLevelData = tmpMenuLvel += 1;
                    const menu = {
                        ...menuItem,
                        firstRegisterTellerID: responseData.body.firstRegisterTellerID,
                        firstRegisterTellerName: `${responseData.body.registerUserFirstName} ${responseData.body.registerUserLastName}`,
                        lastChangeTellerName: `${responseData.body.lastChangeUserFirstName} ${responseData.body.lastChangeUserLastName}`,
                        lastChangeTellerID: responseData.body.lastChangeTellerID
                    }
                    this.selectedMenu = menu
                }
            }


        },

        resetStateTreeData() {
            if (this.menuList.length === 0) return

            this.menuList = this.menuList.map((menu: MenuInfo) => {
                if (menu.state) {
                    menu.state.selected = false
                }
                return menu
            })
        },

        diabledSelectNewMenu() {
            // Disabled select for all menu when have temp new menu
            this.menuList = this.menuList.map((menu: MenuInfo) => {
                if (menu.isTempNewMenu) {
                    menu.state = { indeterminate: false, checked: false, selected: true, expanded: false }
                } else {
                    menu.state = { indeterminate: false, checked: false, selected: false, expanded: false }
                }
                return menu
            });
            this.transformAndExpandTreeData(this.menuList)
        },

        initNewMenu() {
            return {
                isTempNewMenu: true,
                menuID: "",
                parentMenuID: this.selectedMenu.menuID,
                menuName: "New Menu",
                menuStatusCode: "01",
                nodes: [],
                state: { indeterminate: false, checked: false, selected: true, expanded: false }
            }
        },

        onClickAddMenu() {
            if (this.selectedMenu.nodes) {
                this.isInvalidInput = false
                this.isEnableEdit = true
                this.isAddNewMenu = true
                this.resetStateTreeData();
                const newMenu = this.initNewMenu()
                this.menuList = [...this.menuList, newMenu] as MenuInfo[]
                this.transformAndExpandTreeData(this.menuList)
                this.selectedMenu = { ...newMenu, menuName: "" }
                this.onHighLightParentNode();
            } else {
                this.$dialog.onOpen({
                    title: this.$t('message.alert'),
                    showCancel: false,
                    content: this.$t('com205001sc01.please_select_specific_node_before_add'),
                    btnLabel: this.$t('common.check'),
                    callback: () => modalController.dismiss()
                })
            }
        },

        onClickRemoveMenu() {
            // Cannot Delete for menu that have child OR Root Node
            if (this.selectedMenu.menuID === '0' || (this.selectedMenu.nodes && this.selectedMenu.nodes.length > 0)) {
                this.$dialog.onOpen({
                    title: this.$t('message.alert'),
                    showCancel: false,
                    content: this.$t('message.only_the_lowest_level_can_be_deleted'),
                    btnLabel: this.$t('common.check'),
                    callback: () => modalController.dismiss()
                })
            } else {
                this.$dialog.onOpen({
                    title: this.$t('message.alert'),
                    content: this.$t('message.are_you_sure_you_want_to_delete_selected_menu'),
                    callback: () => this.onRemoveMenu()
                })
            }
        },

        async onRemoveMenu() {
            modalController.dismiss()
            const reqBody: COM20500141_REQ_BODY = {
                menuID: this.selectedMenu.menuID
            }
            const responseData = await this.$request.getRequest('COM20500141', reqBody, false) as COM20500141_RES
            if (responseData.header.result) {
                // Temp Remove Local Data
                this.menuList = this.menuList.filter((menu: MenuInfo) => menu.menuID !== this.selectedMenu?.menuID);
                this.transformAndExpandTreeData(this.menuList)
                this.selectedMenu = this.nodeSelectAtRoot()
            }
        },
        
        onCheckValidate() {
            if (this.selectedMenu.menuID === '' || this.selectedMenu.menuName === '') {
                this.isInvalidInput = true
                return true
            }
            this.isInvalidInput = false
            return false
        },

        onClickSave() {
            if (this.onCheckValidate()) {
                this.$dialog.onOpen({
                    title: this.$t('message.alert'),
                    showCancel: false,
                    content: this.$t('message.please_enter_missing_data'),
                    callback: () => modalController.dismiss()
                })
            } else {
                this.$dialog.onOpen({
                    title: this.$t('message.alert'),
                    content: this.$t('message.would_you_like_to_save'),
                    callback: () => {
                        modalController.dismiss()
                        if (this.isAddNewMenu) {
                            this.onRegisterMenuInfo()
                        } else if (!this.isAddNewMenu) {
                            this.onUpdateMenuInfo();
                        }
                    }
                })
            }
        },

        async onUpdateMenuInfo() {
            if (this.selectedMenu && this.selectedMenu.parentMenuID) {
                const reqBody: COM20500131_REQ_BODY = {
                    menuID: this.selectedMenu.menuID,
                    menuName: this.selectedMenu.menuName,
                    parentMenuID: this.selectedMenu.parentMenuID,
                    menuDisplayOrder: Number(this.selectedMenu.menuDisplayOrder),
                    menuLevel: String(this.selectedMenu.menuLevel) as any,
                    menuStatusCode: this.selectedMenu.menuStatusCode as any,
                    menuIconID: '',
                    list: null as any
                }
                const responseData = await this.$request.getRequest('COM20500131', reqBody, false) as COM20500131_RES

                if (responseData.header.result) {
                    this.onUpdateLocalData();
                }
            }
        },

        async onRegisterMenuInfo() {
            if (this.selectedMenu && this.selectedMenu.parentMenuID) {
                const reqBody: COM20500121_REQ_BODY = {
                    menuID: this.selectedMenu.menuID,
                    menuName: this.selectedMenu.menuName,
                    parentMenuID: this.selectedMenu.parentMenuID,
                    menuDisplayOrder: Number(this.selectedMenu.menuDisplayOrder),
                    menuLevel: String(this.menuLevelData) as any,
                    menuStatusCode: this.selectedMenu.menuStatusCode as any,
                    menuIconID: '',
                    list: null as any
                }
                const responseData = await this.$request.getRequest('COM20500121', reqBody, false) as COM20500121_RES
                if (responseData.header.result) {
                    this.onUpdateLocalData();
                }
            }
        },

        onUpdateLocalData() {
            this.transformAndExpandTreeData(this.menuList)
            this.isAddNewMenu = false
            this.isEnableEdit = false
            this.onGetMenuInfoList();
        },

        onClickCancel() {
            if (!this.selectedMenu && this.isEnableEdit) return

            if (this.isAddNewMenu) {
                this.onCancelAddNewMenu()
            } else {
                this.onCancelUpdateMenu()
            }
        },

        onCancelUpdateMenu() {
            this.$dialog.onOpen({
                title: this.$t('message.alert'),
                content: this.$t('message.are_you_sure_you_want_to_cancel_without_saving_data'),
                callback: () => {
                    this.selectedMenu = this.nodeSelectAtRoot()
                    this.isEnableEdit = false
                    modalController.dismiss()
                }
            })
        },

        onCancelAddNewMenu() {
            this.$dialog.onOpen({
                title: this.$t('message.alert'),
                content: this.$t('message.are_you_sure_you_want_to_cancel_without_saving_data'),
                callback: () => this.callbackCancel()
            })
        },

        callbackCancel() {
            this.menuList = this.menuList.filter((menu: MenuInfo) => !menu.isTempNewMenu);
            this.menuList[0].state = { indeterminate: false, checked: false, selected: true, expanded: false }
            this.transformAndExpandTreeData(this.menuList)
            this.selectedMenu = this.nodeSelectAtRoot()
            this.isAddNewMenu = false
            this.isInvalidInput = false
            this.isEnableEdit = false
            this.onResetHighLightParentNode();
            modalController.dismiss()
        },

        onClickEdit() {
            this.isEnableEdit = true
            this.isAddNewMenu = false
        },

        callBackMenuList() {
            this.onGetMenuInfoList();
        },

        onHighLightParentNode() {
            if (this.selectedMenu.isTempNewMenu) {
                let result = this.flattenTree(this.menuTree);
                result.find((list) => {
                    if (list.menuID == this.selectedMenu.parentMenuID) {
                        list.state.highLightParentNode = true;
                    }
                });
            }
        },

        onResetHighLightParentNode() {
            let result = this.flattenTree(this.menuTree);
            result.find((list) => {
                if (list.state.highLightParentNode) {
                    list.state.highLightParentNode = false;
                }
            });
            return result
        },

        // this function I use for Tree data tranform to normal data object to using with highLight parentNode multi level
        flattenTree(tree: any) {
            const flattenedTree = [] as any
            tree.forEach((node: any) => {
                flattenedTree.push(node);
                if (node.nodes.length > 0) {
                    const childNodes = this.flattenTree(node.nodes);
                    flattenedTree.push(...childNodes);
                }
            });
            return flattenedTree;
        }
    }
})
